.card{
    position: relative;
    background: #fff;
    width: 600px;
    padding: 20px;
    max-width: 85vw;
    height: 50vh;
    box-shadow: 0px 18px 53px 0px rgba(0,0,0,0.3);
    border-radius: 20px;
    background-size: cover;
    background-position: center;
}
.tinderCards__cardContainer{
    display: flex;
    justify-content: center;
    margin-top: 10vh;
}
.swipe{
    position: absolute;
}
